import React from 'react';
import { useForm } from "react-hook-form";
import { Firebase, db } from '../../config/firbaseConfig'

import styled from 'styled-components';
import Dropdown from '../Dropdown';

const ConfirmSection = ({ setGetResult, setIsChecking }) => {
  const { register, handleSubmit, formState: { errors, isSubmitting } } = useForm()


  const invalidSelect = (session) => session !== '성회날짜 선택하기';
  
  const onSubmit = (data) => {
    console.log(data)
    const userListRef = db.collection(data.session)
    let indexQuery = userListRef.where("index", "==", 70)
    let query = userListRef.where("name", "==", data.name).where("church", "==", data.church);
    query.get().then(querySnapshot => {
      if (querySnapshot.empty) {
        console.log('No register data')
        setGetResult({
          session: data.session,
          name: data.name,
          church: data.church,
          result: false
        })
      } else {
        querySnapshot.forEach((doc) => {
          if (doc.data().index < 120) {
            console.log('선착순 당첨')
            setGetResult({
              session: data.session,
              name: data.name,
              church: data.church,
              result: true
            })
          } else if (doc.data().index === 120) {
            indexQuery.get().then((querySnapshot) => {
              let tempArray = []
              querySnapshot.forEach((doc, idx) => {
                tempArray.push({
                  id: doc.id,
                  order: idx,
                  ...doc.data()
                })
              })
              tempArray.sort((a, b) => {
                return a.submitTime - b.submitTime
              })
              if (tempArray[0].name === data.name) {
                console.log('선착순당첨')
                setGetResult({
                  session: data.session,
                  name: data.name,
                  church: data.church,
                  result: true
                })
              } else {
                console.log('70명 이후 신청자')
                setGetResult({
                  session: data.session,
                  name: data.name,
                  church: data.church,
                  result: false
                })
              }
            })
          }
        })
      }
    })
    setIsChecking(true)
  }

  return (
    <>
      <Title>신청해주셔서 감사합니다</Title>
      <Subtitle>신청 하신 결과는 아래에서 확인해주세요</Subtitle>
      <FormContainer onSubmit={handleSubmit(onSubmit)}>
        <DropdwownContainer>
          <Dropdown {...register('session', { required: "select one option", validate:invalidSelect})}/>
          {errors.session && <div style={{color: 'red', marginTop: 5, fontSize: '1.4rem'}}>성회일자를 선택해주세요</div>}
        </DropdwownContainer>
        <InputContainer>
          <label htmlFor="church" className="label-church">교회</label>
          <input 
            type="text" 
            name="church" 
            autoComplete="off" 
            className="input-church" 
            {...register('church', { required: true })}
          />
          {errors.church && <div style={{color: 'red', marginTop: 5, marginLeft: 5, fontSize: '1.4rem'}}>교회명을 입력해주세요</div>}
        </InputContainer>
        <InputContainer>
          <label htmlFor="church" className="label-name">이름</label>
          <input 
            type="text" 
            name="name" 
            autoComplete="off" 
            className="input-name"
            {...register('name', { required: true })}
          />
          {errors.name && <div style={{color: 'red', marginTop: 5, marginLeft: 5, fontSize: '1.4rem'}}>이름을 입력해주세요</div>}
        </InputContainer>
        <ButtonContainer>
          <button type="submit" disabled={isSubmitting}>결과확인</button>
        </ButtonContainer>
      </FormContainer>
    </>
  );
}

const Title = styled.h1`
  font-size: 2.2rem;
  color: blue;
  padding: 2.4rem 0;
`;

const Subtitle = styled.h3`
  font-size: 1.6rem;
`;

const DropdwownContainer = styled.div`
  width: 100%;
  margin: 3rem 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
`;

const FormContainer = styled.form`
  display: block;
  width: 100%;
`;

const InputContainer = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 2.4rem;

  label {
    font-size: 1.6rem;
    padding: 0 0 0.3rem;
  }
  input {
    padding : 1.5rem 0.75rem;
    font-size: 1.6rem;
    border: 2px solid rgb(101, 177, 177);
    border-radius: 4px;
    outline: none
  }
`;

const ButtonContainer = styled.div`
  margin-top: 4rem;

  button {
    width: 100%;
    padding: 1.6rem 2rem;
    background: rgb(101, 177, 177);
    border: none;
    border-radius: 4px;
    color: #fff;
    font-size: 1.8rem;
  }
`;

export default ConfirmSection;